      .nav-link {
        border: none;

        &::after {
          content: "";
          background: #7269ef;
          height: 2px;
          position: absolute;
          width: 100%;
          left: 0;
          bottom: -1px;
          -webkit-transition: all 250ms ease 0s;
          transition: all 250ms ease 0s;
          -webkit-transform: scale(0);
          transform: scale(0);
        }

        &.active {
          color: #7269ef;

          &:after {
            -webkit-transform: scale(1);
            transform: scale(1);
          }
        }
      }
    }
